.form__fieldset {
  padding: 0;
}

.form__legend {
}

.form__select {
  padding-left: 15px;
  padding-top: 6px;
  padding-right: 40px;
  padding-bottom: 5px;
  border-radius: 3px;
  border: 1px solid @color-border;
  position: relative;
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  background-image: url(../img/icons/select-arrow-icon.png);
  background-size: 12px 7px;
  background-position: calc(100% - 14px) 12px;
  background-repeat: no-repeat;

  option {
    font-size: 15px;
    line-height: 22px;
    padding-left: 15px;
    padding-top: 6px;
    padding-right: 15px;
    padding-bottom: 5px;
    min-height: 28px;
  }
}

.form__search-input {
  padding-left: 15px;
  padding-top: 6px;
  padding-right: 15px;
  padding-bottom: 5px;
  border: 1px solid @color-border;
}
